import React from 'react';

export default function Aboutus() {
  return (
    <div>
      <div
        className="container-fluid px-0 overflow-hidden page-banner"
        style={{ background: "url(/images/aboutbg.webp)" }}
      >
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item">About</li>
            <li class="breadcrumb-item active" aria-current="page">
              <strong>Vishwa Vishwani</strong>
            </li>
          </ol>
        </nav>
      </div>

      <section className="welcome py-5">
        <div className="container py-3">
          <div className="row d-flex align-items-center">
            <div className="col-lg-6">
              <div className="text-start three">
                <h1 className="display-5 text-primary">
                  <span>About </span>
                  <strong className="fw-bold">Academic Counsel</strong>
                </h1>
                <p>
                  The Academic Counsel at Vishwa Vishwani serves as a guiding
                  force for students, helping them navigate their academic
                  journey and make informed decisions about their education and
                  career paths. Our counsel brings together experienced faculty,
                  industry advisors, and mentors dedicated to student success.
                </p>
                <h3 className="text-warning">Our Purpose</h3>
                <p>
                  To empower students by providing personalized guidance,
                  fostering academic excellence, and preparing them for
                  leadership roles in the global business landscape.
                </p>
              </div>
            </div>
            <div className="col-lg-6">
              <img
                src="/images/academic-counsel.webp"
                className="img-fluid rounded"
                alt="academic"
              />
            </div>
          </div>
        </div>
      </section>

      <section className="py-5 bg-grey academic-bnr">
        <div className="container py-4">
          <div className="row d-flex justify-content-center align-items-center">
            <div className="col-lg-8 one">
              <h1>Objectives</h1>
              <p>
                The primary goal of the Academic Counsel is to provide students
                with a clear roadmap for their academic and professional
                journeys. By helping students choose the right courses,
                electives, and specializations, we align their education with
                their career goals. Our counsel focuses on skill development,
                encouraging students to identify areas for improvement and take
                proactive steps to enhance their capabilities. We also emphasize
                continuous mentorship and academic support, fostering a culture
                of excellence and lifelong learning.
              </p>
            </div>
          </div>
        </div>
      </section>

      <section className='whycourse py-5'>
        <div className='container'>
            <div className='one'>
            <h1 className='display-5 text-primary mb-4'>
              Activities and Services
            </h1>
            </div>
            <div className='row'>
                <div className='col-lg-4'>
                    <div className="card">
                        <div className="card-body">
                            <h5 className="card-title">One-on-One Counseling</h5>
                            <p className="card-text">Personalized sessions with academic advisors to address individual needs.</p>
                        </div>
                    </div>
                </div>
                <div className='col-lg-4'>
                    <div className="card">
                        <div className="card-body">
                            <h5 className="card-title">Workshops</h5>
                            <p className="card-text">Skill-building workshops on topics like time management, stress handling, and goal setting.</p>
                        </div>
                    </div>
                </div>
                <div className='col-lg-4'>
                    <div className="card">
                        <div className="card-body">
                            <h5 className="card-title">Peer Mentorship Programs</h5>
                            <p className="card-text">Learn from seniors and alumni who have walked the same path.</p>
                        </div>
                    </div>
                </div>
                <div className='col-lg-4'>
                    <div className="card">
                        <div className="card-body">
                            <h5 className="card-title">Periodic Reviews</h5>
                            <p className="card-text">Regular assessments to ensure students stay on track with their academic goals.</p>
                        </div>
                    </div>
                </div>
                <div className='col-lg-4'>
                    <div className="card">
                        <div className="card-body">
                            <h5 className="card-title">Industry Insights</h5>
                            <p className="card-text">Sessions with professionals to bridge the gap between academics and real-world expectations.</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </section>

    </div>
  );
}
