import React from 'react';

export default function BoardofStudies() {
  return (
    <div>
      <div
        className="container-fluid px-0 overflow-hidden page-banner"
        style={{ background: "url(/images/aboutbg.webp)" }}
      >
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item">About</li>
            <li class="breadcrumb-item active" aria-current="page">
              <strong>Board of Studies</strong>
            </li>
          </ol>
        </nav>
      </div>

      <section className="welcome py-5">
        <div className="container py-3">
          <div className="row d-flex align-items-center">
            <div className="col-lg-6">
              <div className="text-start three">
                <h1 className="display-5 text-primary">
                  <span>About the </span>
                  <strong className="fw-bold"> Board of Studies</strong>
                </h1>
                <p>
                  The Board of Studies at Vishwa Vishwani plays a pivotal role
                  in shaping the academic framework of the institution. It is a
                  body of distinguished academicians, industry experts, and
                  experienced faculty members dedicated to ensuring that our
                  curriculum remains relevant, dynamic, and aligned with the
                  latest industry trends and global standards.
                </p>
                <h3 className="text-warning">Objectives</h3>
                <p>
                  The primary objective of the Board of Studies is to design and
                  implement a robust curriculum that empowers students with
                  knowledge, skills, and values essential for success. By
                  integrating industry insights with academic rigor, the board
                  ensures that our programs equip students to tackle real-world
                  challenges. Additionally, it seeks to foster innovation and
                  continuous improvement in teaching methodologies to enhance
                  the overall learning experience.
                </p>
              </div>
            </div>
            <div className="col-lg-6">
              <img
                src="/images/boardofstudy.webp"
                className="img-fluid rounded"
                alt="academic"
              />
            </div>
          </div>
        </div>
      </section>

      <section className="py-5 bg-grey academic-bnr">
        <div className="container py-4">
          <div className="row d-flex justify-content-center align-items-center">
            <div className="col-lg-8 one">
              <h1>What the Board of Studies Ensures</h1>
              <p>
                The Board of Studies ensures that students receive a
                high-quality education that bridges the gap between academia and
                industry. It promotes critical thinking, innovation, and
                lifelong learning by incorporating interdisciplinary approaches
                and emerging technologies into the curriculum. The board also
                ensures the inclusion of global perspectives to prepare students
                for success in an interconnected world.
              </p>
            </div>
          </div>
        </div>
      </section>

      <section className="whycourse py-5">
        <div className="container">
          <div className="one">
            <h1 className="display-5 text-primary mb-4">
              Activities and Responsibilities
            </h1>
            <p>The Board of Studies engages in various activities to uphold academic excellence</p>
          </div>
          <div className="row">
            <div className="col-lg-4">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">Curriculum Development</h5>
                  <p className="card-text">
                  Regularly reviewing and updating course content to reflect the latest industry practices and academic advancements.
                  </p>
                </div>
              </div>
            </div>
            <div className="col-lg-4">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">Program Innovation:</h5>
                  <p className="card-text">
                  Introducing new courses, electives, and specializations that align with market demands and emerging fields.
                  </p>
                </div>
              </div>
            </div>
            <div className="col-lg-4">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">Quality Assurance</h5>
                  <p className="card-text">
                  Establishing benchmarks for teaching, assessment, and learning outcomes to maintain high academic standards.
                  </p>
                </div>
              </div>
            </div>
            <div className="col-lg-4">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">Collaboration</h5>
                  <p className="card-text">
                  Partnering with industry leaders and academic institutions to integrate real-world applications into the curriculum.
                  </p>
                </div>
              </div>
            </div>
            <div className="col-lg-4">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">Workshops and Training</h5>
                  <p className="card-text">
                  Organizing faculty development programs to enhance teaching effectiveness and keep educators updated on new trends.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      
    </div>
  );
}
