import React from "react";
import { NavLink } from "react-router-dom";

export default function MBA() {
  return (
    <div>
      <div
        className="container-fluid px-0 overflow-hidden page-banner"
        style={{ background: "url(/images/aboutbg.webp)" }}
      >
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item">Study</li>
            <li class="breadcrumb-item active" aria-current="page">
              <strong>Master of Business Administration (MBA)</strong>
            </li>
          </ol>
        </nav>
      </div>

      <div className="container pt-5">
        <div className="row">
          <div className="col-lg-2">
            <div className="vert-menu">
              <ul>
                <li>
                  <NavLink
                    to="/bba"
                    className={({ isActive }) =>
                      isActive ? "vert-m-link active" : "vert-m-link"
                    }
                  >
                    BBA
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/mba"
                    className={({ isActive }) =>
                      isActive ? "vert-m-link active" : "vert-m-link"
                    }
                  >
                    MBA
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/imba"
                    className={({ isActive }) =>
                      isActive ? "vert-m-link active" : "vert-m-link"
                    }
                  >
                    IMBA
                  </NavLink>
                </li>
              </ul>
            </div>
          </div>
          <div className="col-lg-10">
            <div className="row">
              <div className="col-lg-12">
                <div className="page-content-view">
                  <h3 className="mt-0 mb-2">
                    Master of Business Administration (MBA)
                  </h3>
                  <p>
                    The Master of Business Administration (MBA) program at
                    Vishwa Vishwani is designed to create business leaders and
                    innovators who can thrive in a competitive global market.
                    This two-year program integrates a robust curriculum,
                    real-world applications, and a strong focus on leadership
                    development, empowering students to make impactful decisions
                    and drive success in diverse industries.
                  </p>
                  <p>
                    Our MBA program emphasizes experiential learning through
                    case studies, internships, and live projects, providing
                    students with the skills and knowledge needed to excel in
                    management roles.
                  </p>

                  <div className="one bg-grey p-4 my-5 rounded">
                    <h1>About the Department</h1>
                    <p className="text-center">
                      The Department of Management Studies at Vishwa Vishwani is
                      the cornerstone of excellence in business education. It is
                      dedicated to nurturing future leaders, entrepreneurs, and
                      innovators through a combination of academic rigor,
                      practical learning, and industry exposure. The department
                      offers comprehensive programs like MBA, BBA, and IMBA,
                      designed to meet the ever-evolving demands of the global
                      business landscape.
                    </p>
                  </div>

                  <div class="row services one">
                    <h1 className="text-primary my-4">
                      Why Choose MBA at Vishwa Vishwani?
                    </h1>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-solid fa-clipboard display-5 mb-3"></i>
                        <h2>Comprehensive Curriculum</h2>
                        <p class="text-center mb-0">
                        A blend of theoretical and practical knowledge aligned with industry standards.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>Specializations</h2>
                        <p class="text-center mb-0">
                        Choose from a wide range of specializations, including Finance, Marketing, Human Resources, Operations, and International Business.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>Global Exposure</h2>
                        <p class="text-center mb-0">
                        Opportunities to collaborate on international projects and participate in exchange programs.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>Industry-Ready Graduates:</h2>
                        <p class="text-center mb-0">
                        Internship programs and live projects prepare students for real-world challenges.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>Expert Faculty</h2>
                        <p class="text-center mb-0">
                        Learn from seasoned academicians and industry leaders who bring years of experience and insights.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>State-of-the-Art Infrastructure</h2>
                        <p class="text-center mb-0">
                        Modern facilities and resources to enhance learning and research.
                        </p>
                      </div>
                    </div>
                  </div>

                  <div class="row one py-5">
                    <h1 className="text-primary">Highlights</h1>
                    <div class="accordion" id="accordionExample">
                      <div class="accordion-item">
                        <h2 class="accordion-header" id="headingOne">
                          <button
                            class="accordion-button"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#collapseOne"
                            aria-expanded="true"
                            aria-controls="collapseOne"
                          >
                            Duration
                          </button>
                        </h2>
                        <div
                          id="collapseOne"
                          class="accordion-collapse collapse show"
                          aria-labelledby="headingOne"
                          data-bs-parent="#accordionExample"
                        >
                          <div class="accordion-body">
                          2 Years (Full-Time)
                          </div>
                        </div>
                      </div>
                      <div class="accordion-item">
                        <h2 class="accordion-header" id="headingTwo">
                          <button
                            class="accordion-button collapsed"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#collapseTwo"
                            aria-expanded="false"
                            aria-controls="collapseTwo"
                          >
                            Eligibility
                          </button>
                        </h2>
                        <div
                          id="collapseTwo"
                          class="accordion-collapse collapse"
                          aria-labelledby="headingTwo"
                          data-bs-parent="#accordionExample"
                        >
                          <div class="accordion-body">
                          Bachelors degree with a minimum of 50% marks (45% for reserved categories)
                          </div>
                        </div>
                      </div>
                      <div class="accordion-item">
                        <h2 class="accordion-header" id="headingThree">
                          <button
                            class="accordion-button collapsed"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#collapseThree"
                            aria-expanded="false"
                            aria-controls="collapseThree"
                          >
                            Admission Process
                          </button>
                        </h2>
                        <div
                          id="collapseThree"
                          class="accordion-collapse collapse"
                          aria-labelledby="headingThree"
                          data-bs-parent="#accordionExample"
                        >
                          <div class="accordion-body">
                          Entrance test followed by Group Discussion (GD) and Personal Interview (PI)
                          </div>
                        </div>
                      </div>
                      <div class="accordion-item">
                        <h2 class="accordion-header" id="headingFour">
                          <button
                            class="accordion-button collapsed"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#collapseFour"
                            aria-expanded="false"
                            aria-controls="collapseFour"
                          >
                            Certification
                          </button>
                        </h2>
                        <div
                          id="collapseFour"
                          class="accordion-collapse collapse"
                          aria-labelledby="headingFour"
                          data-bs-parent="#accordionExample"
                        >
                          <div class="accordion-body">
                          MBA degree recognized by [relevant accrediting body]
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <section className="py-5 bg-grey">
        <div className="container">
          <div className="row one">
            <h1 className="text-danger">MBA Program Structure</h1>
            <p className="text-center">
            The MBA program is divided into four semesters, offering a mix of core subjects and electives to suit individual career goals.
            </p>
            <div className="col-lg-12">
              <table class="table table-bordered table-striped table-hover text-center">
                <thead class="table-warning">
                  <tr>
                    <th scope="col">
                    Semester 1
                    </th>
                    <th scope="col">
                    Semester 2
                    </th>
                    <th scope="col">
                    Semester 3
                    </th>
                    <th scope="col">
                    Semester 4
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>Principles of Management</td>
                    <td>Marketing Management</td>
                    <td>Strategic Management</td>
                    <td>Entrepreneurship Development</td>
                  </tr>

                  <tr>
                    <td>Business Statistics</td>
                    <td>Human Resource Management</td>
                    <td>International Business</td>
                    <td>Business Analytics</td>
                  </tr>

                  <tr>
                    <td>Financial Management</td>
                    <td>Operations Management</td>
                    <td>Advanced Electives in Specialization</td>
                    <td>Capstone Project</td>
                  </tr>

                  <tr>
                    <td>Managerial Economics</td>
                    <td>Research Methodology</td>
                    <td>Project Work</td>
                    <td>Advanced Topics in Specialization</td>
                  </tr>

                  <tr>
                    <td>Organizational Behavior</td>
                    <td>Business Law and Ethics</td>
                    <td>Industry Internship</td>
                    <td>Comprehensive Viva</td>
                  </tr>

                  <tr>
                    <td>Business Communication</td>
                    <td>Electives</td>
                    <td>-</td>
                    <td>-</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </section>

      <section className="py-5">
        <div className="container">
          <div className="row one">
            <h1 className="text-primary my-4">Faculty</h1>
            <div className="col-lg-3">
              <div class="card">
                <img
                  src="/images/lecturer-1.webp"
                  class="card-img-top"
                  alt="..."
                />
                <div class="card-body text-center">
                  <h5 class="card-title">Faculty Name</h5>
                  <p class="card-text">Experience</p>
                  <a href="#" class="btn btn-primary">
                    View Profile
                  </a>
                </div>
              </div>
            </div>
            <div className="col-lg-3">
              <div class="card">
                <img
                  src="/images/lecturer-1.webp"
                  class="card-img-top"
                  alt="..."
                />
                <div class="card-body text-center">
                  <h5 class="card-title">Faculty Name</h5>
                  <p class="card-text">Experience</p>
                  <a href="#" class="btn btn-primary">
                    View Profile
                  </a>
                </div>
              </div>
            </div>
            <div className="col-lg-3">
              <div class="card">
                <img
                  src="/images/lecturer-1.webp"
                  class="card-img-top"
                  alt="..."
                />
                <div class="card-body text-center">
                  <h5 class="card-title">Faculty Name</h5>
                  <p class="card-text">Experience</p>
                  <a href="#" class="btn btn-primary">
                    View Profile
                  </a>
                </div>
              </div>
            </div>
            <div className="col-lg-3">
              <div class="card">
                <img
                  src="/images/lecturer-1.webp"
                  class="card-img-top"
                  alt="..."
                />
                <div class="card-body text-center">
                  <h5 class="card-title">Faculty Name</h5>
                  <p class="card-text">Experience</p>
                  <a href="#" class="btn btn-primary">
                    View Profile
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
