import React from "react";
import { NavLink } from "react-router-dom";

export default function IMBA() {
  return (
    <div>
      <div
        className="container-fluid px-0 overflow-hidden page-banner"
        style={{ background: "url(/images/aboutbg.webp)" }}
      >
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item">Study</li>
            <li class="breadcrumb-item active" aria-current="page">
              <strong>Integrated Master of Business Administration (IMBA)</strong>
            </li>
          </ol>
        </nav>
      </div>

      <div className="container pt-5">
        <div className="row">
          <div className="col-lg-2">
            <div className="vert-menu">
              <ul>
                <li>
                  <NavLink
                    to="/bba"
                    className={({ isActive }) =>
                      isActive ? "vert-m-link active" : "vert-m-link"
                    }
                  >
                    BBA
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/mba"
                    className={({ isActive }) =>
                      isActive ? "vert-m-link active" : "vert-m-link"
                    }
                  >
                    MBA
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/imba"
                    className={({ isActive }) =>
                      isActive ? "vert-m-link active" : "vert-m-link"
                    }
                  >
                    IMBA
                  </NavLink>
                </li>
              </ul>
            </div>
          </div>
          <div className="col-lg-10">
            <div className="row">
              <div className="col-lg-12">
                <div className="page-content-view">
                  <h3 className="mt-0 mb-2">
                  Integrated Master of Business Administration (IMBA)
                  </h3>
                  <p>
                  The Integrated Master of Business Administration (IMBA) program at Vishwa Vishwani is an innovative five-year course designed for aspiring business leaders who wish to gain an advanced understanding of management principles right after completing high school. This dual-degree program seamlessly combines undergraduate and postgraduate studies, offering students a comprehensive education in business and management.
                  </p>
                  <p>
                  The IMBA curriculum is crafted to provide a strong foundation in business concepts during the initial years and advanced management skills in the later stages. This approach ensures that students are well-equipped to tackle complex challenges in the global business landscape.
                  </p>

                  <div className="one bg-grey p-4 my-5 rounded">
                    <h1>About the Department</h1>
                    <p className="text-center">
                    The Department of Integrated Management Studies at Vishwa Vishwani is a trailblazer in delivering holistic, long-term management education. Focused on developing skilled professionals, the department offers the prestigious Integrated Master of Business Administration (IMBA) program, which seamlessly combines undergraduate and postgraduate education over five years.
                    </p>
                  </div>

                  <div class="row services one">
                    <h1 className="text-primary my-4">
                      Why Choose IMBA at Vishwa Vishwani?
                    </h1>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-solid fa-clipboard display-5 mb-3"></i>
                        <h2>Integrated Learning</h2>
                        <p class="text-center mb-0">
                        A unique combination of undergraduate and postgraduate studies ensures holistic development.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>Comprehensive Curriculum</h2>
                        <p class="text-center mb-0">
                        A blend of foundational knowledge and advanced business strategies.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>Global Perspective</h2>
                        <p class="text-center mb-0">
                        Exposure to international business practices through collaborations and case studies.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>Practical Learning</h2>
                        <p class="text-center mb-0">
                        Hands-on experience through internships, live projects, and industry visits.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>Future-Ready</h2>
                        <p class="text-center mb-0">
                        Graduates are prepared for leadership roles or entrepreneurial ventures right after completing the program.
                        </p>
                      </div>
                    </div>
                    <div class="col-lg-4 mb-4">
                      <div class="card shadow p-4 d-flex flex-column justify-content-center align-items-center">
                        <div class="anim-layer"></div>
                        <i class="fa-regular fa-clipboard display-5 mb-3"></i>
                        <h2>Cost-Effective</h2>
                        <p class="text-center mb-0">
                        A streamlined program saves time and resources compared to pursuing separate degrees.
                        </p>
                      </div>
                    </div>
                  </div>

                  <div class="row one py-5">
                    <h1 className="text-primary">Highlights</h1>
                    <div class="accordion" id="accordionExample">
                      <div class="accordion-item">
                        <h2 class="accordion-header" id="headingOne">
                          <button
                            class="accordion-button"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#collapseOne"
                            aria-expanded="true"
                            aria-controls="collapseOne"
                          >
                            Duration
                          </button>
                        </h2>
                        <div
                          id="collapseOne"
                          class="accordion-collapse collapse show"
                          aria-labelledby="headingOne"
                          data-bs-parent="#accordionExample"
                        >
                          <div class="accordion-body">
                          5 Years (Full-Time)
                          </div>
                        </div>
                      </div>
                      <div class="accordion-item">
                        <h2 class="accordion-header" id="headingTwo">
                          <button
                            class="accordion-button collapsed"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#collapseTwo"
                            aria-expanded="false"
                            aria-controls="collapseTwo"
                          >
                            Eligibility
                          </button>
                        </h2>
                        <div
                          id="collapseTwo"
                          class="accordion-collapse collapse"
                          aria-labelledby="headingTwo"
                          data-bs-parent="#accordionExample"
                        >
                          <div class="accordion-body">
                          10+2 or equivalent with a minimum of 50% marks
                          </div>
                        </div>
                      </div>
                      <div class="accordion-item">
                        <h2 class="accordion-header" id="headingThree">
                          <button
                            class="accordion-button collapsed"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#collapseThree"
                            aria-expanded="false"
                            aria-controls="collapseThree"
                          >
                            Admission Process
                          </button>
                        </h2>
                        <div
                          id="collapseThree"
                          class="accordion-collapse collapse"
                          aria-labelledby="headingThree"
                          data-bs-parent="#accordionExample"
                        >
                          <div class="accordion-body">
                          Entrance exam followed by a Personal Interview
                          </div>
                        </div>
                      </div>
                      <div class="accordion-item">
                        <h2 class="accordion-header" id="headingFour">
                          <button
                            class="accordion-button collapsed"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#collapseFour"
                            aria-expanded="false"
                            aria-controls="collapseFour"
                          >
                            Degree Awarded
                          </button>
                        </h2>
                        <div
                          id="collapseFour"
                          class="accordion-collapse collapse"
                          aria-labelledby="headingFour"
                          data-bs-parent="#accordionExample"
                        >
                          <div class="accordion-body">
                          Integrated Master of Business Administration
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <section className="py-5 bg-grey">
        <div className="container">
            <div className="row one">
                <h1 className="text-danger">MBA Program Structure</h1>
                <p className="text-center">The IMBA program spans five years and is divided into 10 semesters.</p>
                <div className="col-lg-12">
                    <table class="table table-bordered table-striped table-hover text-center">
                        <thead class="table-warning">
                            <tr>
                                <th scope="col">Year 1 <br/> Fundamentals of Business and Management</th>
                                <th scope="col">Year 2 <br/> Intermediate Concepts and Applications</th>
                                <th scope="col">Year 3 <br/> Advanced Specialization and Strategy</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>Principles of Management</td>
                                <td>Organizational Behavior</td>
                                <td>Strategic Management</td>
                            </tr>
                            
                            <tr>
                                <td>Business Communication</td>
                                <td>Marketing Management</td>
                                <td>Financial Management</td>
                            </tr>
                            
                            <tr>
                                <td>Financial Accounting</td>
                                <td>Cost Accounting</td>
                                <td>Human Resource Management</td>
                            </tr>
                            
                            <tr>
                                <td>Microeconomics</td>
                                <td>Business Law</td>
                                <td>Entrepreneurship Development</td>
                            </tr>
                            
                            <tr>
                                <td>Business Mathematics</td>
                                <td>Macroeconomics</td>
                                <td>International Business</td>
                            </tr>
                            
                            <tr>
                                <td>Computer Applications</td>
                                <td>Business Statistics</td>
                                <td>Project Work/Internship</td>
                            </tr>
                            
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
      </section>

      <section className="py-5">
        <div className="container">
            <div className="row one">
                <h1 className="text-primary my-4">
                    Faculty
                </h1>
                <div className="col-lg-3">
                    <div class="card">
                        <img src="/images/lecturer-1.webp" class="card-img-top" alt="..." />
                        <div class="card-body text-center">
                            <h5 class="card-title">Faculty Name</h5>
                            <p class="card-text">Experience</p>
                            <a href="#" class="btn btn-primary">View Profile</a>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3">
                    <div class="card">
                        <img src="/images/lecturer-1.webp" class="card-img-top" alt="..." />
                        <div class="card-body text-center">
                            <h5 class="card-title">Faculty Name</h5>
                            <p class="card-text">Experience</p>
                            <a href="#" class="btn btn-primary">View Profile</a>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3">
                    <div class="card">
                        <img src="/images/lecturer-1.webp" class="card-img-top" alt="..." />
                        <div class="card-body text-center">
                            <h5 class="card-title">Faculty Name</h5>
                            <p class="card-text">Experience</p>
                            <a href="#" class="btn btn-primary">View Profile</a>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3">
                    <div class="card">
                        <img src="/images/lecturer-1.webp" class="card-img-top" alt="..." />
                        <div class="card-body text-center">
                            <h5 class="card-title">Faculty Name</h5>
                            <p class="card-text">Experience</p>
                            <a href="#" class="btn btn-primary">View Profile</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </section>

    </div>
  );
}
